.project {
  display: flex;
  flex-direction: column;
}
.project .projectList .plMain .plSearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.project .projectList .plMain .plSearchBar .plPulldown {
  display: flex;
  flex-direction: row-reverse;
}
.project .newPro {
  margin: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
}
.project .newPro .PTCBtn {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  grid-gap: 30px;
  padding: 1rem 0.8rem;
  justify-content: center;
}
.project .newPro .site-page-header {
  width: 100%;
}
.project .newPro .newProForm {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.project .newPro .newProForm .formBtn {
  display: flex;
  justify-content: center;
}
.NPMembersContainer {
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
